import React,{ useState } from "react";
import { Layout, Menu, Row } from "antd";
const { Sider } = Layout;
import { manageemp } from "../../assets/icons/Icon";
import {
  MenuUnfoldOutlined,
  MenuFoldOutlined,
  UserOutlined,
  VideoCameraOutlined,
  UploadOutlined,
} from "@ant-design/icons";

class AccSidebar extends React.Component {
  state = {
    collapsed: false,
  };

  toggle = () => {
    console.log("this.state.collapsed",this.state.collapsed)
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  render() {
    return (
      <>
              {React.createElement(
            this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined,
            {
              className: "trigger d-lg-none d-block",
              onClick: this.toggle,
            }

          )}
      <Sider className={this.state.collapsed ? "d-none d-lg-block w-md-100 sider-width" : " d-lg-block w-md-100 sider-width"} trigger={null} collapsible collapsed={this.state.collapsed}>
      <Menu theme="dark" mode="inline" defaultSelectedKeys={["1"]}>
        <Menu.Item key="1" icon={<img src={manageemp} />}>
          {" "}
          Manage Organizations
        </Menu.Item>
      </Menu>
      <div className="btm-div">
        <a href="#" className="px-2">
          Info
        </a>
        <a href="#" className="px-2">
          Help
        </a>
      </div>
    </Sider>
        </>
    );
  }
}

export default AccSidebar;
